  &__heading {
    
  }
  &__body {
    padding: .5em;
  }
  ul {
    padding: .5em 1em;
  }
  &__group-label {
    // position: sticky;
    flex: 1 1 100%;
    // top: 32px;
    z-index: 5;
    // &.--segment {
    //   margin: .5em -.5em;
    // }
    &.--top {
      top: 0;
      z-index: 10;
      display: flex;
      margin-bottom: 1em;
      &> span {
        display: flex;
        flex: 1 0 50%;
        align-items: center;
        &.--left {
          font-weight: bold;
        }
        &.--right {
          justify-content: flex-end;
        }
      }
    }
    line-height: 32px;
    display: flex;
  }
  &__field-label {
    font-weight: bold;
    font-size: .75em;
    line-height: 1em;
    margin-bottom: .5em;
    display: flex;
    align-items: center;
    &> label {
      margin-right: .5em;
    }
  }
  &__field-list {
    li {
      &+ li {
        margin-top: .5em;
      }
    }
    label {
      display: flex;
    }
    input {
      flex: 1 1 auto;
      margin: 0 0 0 .5em;
    }
    &.--key-list {
      box-shadow: inset 0 0 0 1px rgba(67, 90, 111, 0.2);
      border-radius: .25em;
      background: rgba(67, 90, 111, 0.025);
      padding: .5em;
      .form-block__field {
        flex-direction: row;
        label {
          flex: 1 1 50%;
          margin-bottom: 0;
          flex-flow: row-reverse;
          padding-right: .5em;
          &+ input {
            flex: 1 1 50%;
          }
        }
        input {
          margin-left: 0;
        } 
        &-label {
          flex-direction: row-reverse;
          label {
            flex: 1 1 50%;
            margin: 0 0 0 .5em;
            &+ span {
              flex: 1 1 50%;
              display: flex;
              justify-content: flex-end;
            }
          }
        }
      }
    }
  }
  &__field-toggle[type="checkbox"] {
    margin-right: 5px;
  }
  &__field-describe {
    display: block;
    font-size: .75em;
    line-height: 1.2em;
    padding-bottom: .5em;
    opacity: 0.5;
  }
  &__field-value {
    margin-left: .5em;
  }
  .form-field {
    &+ .form-field {
      margin-top: 1em;
      padding-top: 1em;
      border-top: 1px solid rgba(255,255,255,0.1);
    }
    &.--error {
      border-left: .25em solid red;
      background: rgba(255,0,0,0.05);
      .form-block {
        &__field-label,
        &__field-toggle[type="checkbox"] {
          color: red;
        }
      }
    }
  }
  &__field {
    display: flex;
    flex-direction: column;
  }
}