@import 'reset';

body {
  margin: 0;
  background: #fff;
  color: #425a70;
  font-family: "SF UI Text", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

a {
  text-decoration: none;
}
a:visited {
  color: inherit
}

.nav__item {
  span {
    display: flex;
    align-items: center;
  }
}

#root {
  main {
    display: flex;
    flex-direction: column;
    height: 100vh;
    &> header {
      display: flex;
      flex: 0 0 50px;
      justify-content: space-between;
      background-color: #fff;
      box-shadow: 0 0 1px rgba(67,90,111,.47),0 2px 4px -2px rgba(67,90,111,.3);
      z-index: 10;
      .nav {
        display: flex;
        padding: 0 .5em;
        &__item {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-left: .5em;
          span {
            display: flex;
            align-items: center;
          }
          &.--active:hover,
          &.--active {
            button {
              background-color: rgba(16, 112, 202, 0.09);
              color: #1070ca;
            }
          }
        }
      }
    }
    &> section {
      display: flex;
      flex: 1 1 auto;
      .sidenav {
        flex: 0 0 200px;
        overflow-y: auto;
        padding: .5em;
        display: flex;
        flex-direction: column;
        background: #f5f6f7;
        font-size: .85em;
        &__item {
          padding: .25em .5em;
          &> span > span {
            display: flex;
            align-items: center;
            svg {
              margin-right: 1em;
            }
          }
          &:hover,
          &.--active {
            color: #1070ca;
          }
        }
      }
      &> article {
        flex: 1 1 auto;
      }
    }
    .login-center {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100vw;
      height: 100vh;
    }
  }
}

.navlink__icon svg {
  height: 24px;
  width: 24px;
  margin-bottom: .25em;
  fill: #234361 !important;
}

.griditem__block {
  // border: 2px solid transparent;
  a {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  transition: border-color 0.25s;
  &:hover {
    border-width: 3px;
    border-color: #1070ca;
    cursor: pointer;
  }
}

@import "split-content";
@import "forms";
@import "buttons";

@import "code-editor";