.split-content {
  position: relative;
  display: flex;
  height: calc(100vh - 50px);
  &__left,
  &__right {
    flex: 1 1 50%;
    max-height: 100%;
    overflow: auto;
  }
  &__left {
    // flex: 1 1 50%;
  }
  &__right {
    background-color: #f5f6f7;
    // border-left: .5em solid #fff;
  }
  &__heading {
    // position: sticky;
    flex: 1 1 100%;
    background: #000;
    // top: 0;
    padding: .5em;
  }
  .content-busy {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
}